import React, { useState, useEffect, Fragment, useContext } from 'react';
import { PaletteContext } from 'contexts/palette.context';
import useInputState from 'hooks/useInputState';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator';
import { Picker } from 'emoji-mart';
import { ThemeContext } from 'contexts/theme.context';

export default function SavePaletteDialog(props) {
  const palettes = useContext(PaletteContext);

  const theme = useContext(ThemeContext);
	const background = {
		backgroundColor : theme.darkMode ? '#333' : '#f6f7fb'
	};

	const [ stage, setStage ] = useState('form');
	const [ newPaletteName, setNewPaletteName, resetNewPaletteName ] = useInputState();
	
	const selectEmoji = (emoji) => {
		setStage('');
		props.savePalette(newPaletteName, emoji.native);
	};

	const hideDialog = () => {
		setStage('form');
		resetNewPaletteName();
		props.toggleDialog();
	};

	const onNameSubmit = () => setStage('emoji');

	useEffect(() => {
		ValidatorForm.addValidationRule('uniquePaletteName', (value) =>
			palettes.every(
				({ paletteName }) => paletteName.toLowerCase() !== value.toLowerCase()
			)
    );
    theme.darkMode ? require('styles/emoji-mart/dark.css') : require('styles/emoji-mart/light.css');
	}, []);

	return (
		<Fragment>
			<Dialog open={props.open && stage === 'emoji'} onClose={hideDialog}>
				<Picker onSelect={selectEmoji} title='Pick an Emoji' native={true} />
			</Dialog>
			<Dialog open={props.open && stage === 'form'} onClose={props.toggleDialog}>
				<ValidatorForm onSubmit={onNameSubmit}>
					<DialogTitle style={background}>Palette Name</DialogTitle>
					<DialogContent style={background}>
						<DialogContentText>
							Please enter a name for your amazing palette and make sure
							it's unique from the rest.
						</DialogContentText>
						<TextValidator
							fullWidth
							autoFocus
							value={newPaletteName}
							name='newPaletteName'
							placeholder='Enter a Palette Name'
							onChange={setNewPaletteName}
							validators={[ 'required', 'uniquePaletteName' ]}
							errorMessages={[
								'Palette Name is Required',
								'Palette Name Already Taken'
							]}
						/>
						<DialogActions style={background}>
							<Button onClick={props.toggleDialog} color='secondary'>
								Cancel
							</Button>
							<Button type='submit' color='primary'>
								Pick Emoji
							</Button>
						</DialogActions>
					</DialogContent>
				</ValidatorForm>
			</Dialog>
		</Fragment>
	);
}
